import hash from '@emotion/hash';
import { SEQUENCE_HASH_LENGTH, SEQUENCE_PREFIX } from '../../constants';

function padEndHash(value) {
  var hashLength = value.length;

  if (hashLength === SEQUENCE_HASH_LENGTH) {
    return value;
  }

  for (var i = hashLength; i < SEQUENCE_HASH_LENGTH; i++) {
    value += '0';
  }

  return value;
}

export function hashSequence(classes, dir) {
  return SEQUENCE_PREFIX + padEndHash(hash(classes + dir));
}
